.about-me{
  animation: fadeIn 7s;
  margin-top: 32px;
  line-height: 2;
  border-style: ridge;
  border-color: white;
  border-radius: 8px;
  width: 50vw;
  padding: 20px;
}
.about-me p{
  font-size: 20px;
}
.about-me h2{
  text-decoration: inset;
}
.quote{
  animation: fadeIn 7s;
  font-size: 20px;
  margin-top: 100px;
  line-height: 1.5;
  border-style: ridge;
  border-color: white;
  border-radius: 8px;
  width: fit-content;
  text-align: right;
  padding: 20px;
  float: right;
}
.homepage{
  margin: 20px;
}
@keyframes fadeIn {
from{
  opacity: 0;
}
to{
  opacity: 1;
}
}
@media screen and (max-width: 680px) {
.about-me{
  width: 80vw;
  border-style: none;
  border-style: ridge;
}
.about-me h2{
  font-size: small;
}
.about-me p{
  font-size: small;
}
.quote h2{
  font-size: small;
  margin-bottom: 10px;
}
.quote p{
  font-size: small;
}
.quote{
  margin-top: 50px;
  text-align: left;
  float: left;
}
}