@import url(https://fonts.googleapis.com/css2?family=Tiro+Gurmukhi:ital@1&display=swap);
*{
  margin: 0;
  font-family: "TiroGurmukhi";
  color: white;
}
.about-me{
  -webkit-animation: fadeIn 7s;
          animation: fadeIn 7s;
  margin-top: 32px;
  line-height: 2;
  border-style: ridge;
  border-color: white;
  border-radius: 8px;
  width: 50vw;
  padding: 20px;
}
.about-me p{
  font-size: 20px;
}
.about-me h2{
  -webkit-text-decoration: inset;
          text-decoration: inset;
}
.quote{
  -webkit-animation: fadeIn 7s;
          animation: fadeIn 7s;
  font-size: 20px;
  margin-top: 100px;
  line-height: 1.5;
  border-style: ridge;
  border-color: white;
  border-radius: 8px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  text-align: right;
  padding: 20px;
  float: right;
}
.homepage{
  margin: 20px;
}
@-webkit-keyframes fadeIn {
from{
  opacity: 0;
}
to{
  opacity: 1;
}
}
@keyframes fadeIn {
from{
  opacity: 0;
}
to{
  opacity: 1;
}
}
@media screen and (max-width: 680px) {
.about-me{
  width: 80vw;
  border-style: none;
  border-style: ridge;
}
.about-me h2{
  font-size: small;
}
.about-me p{
  font-size: small;
}
.quote h2{
  font-size: small;
  margin-bottom: 10px;
}
.quote p{
  font-size: small;
}
.quote{
  margin-top: 50px;
  text-align: left;
  float: left;
}
}
.App {
  -webkit-animation: fadeIn 1s;
          animation: fadeIn 1s;
  background-color: #242424;
  background-size: cover;
  position: fixed;
  width: 100%;
  height: 100%;
  background-position: center;
}
@media screen and (max-width : 680px) {
  .App{
    overflow: scroll;
  }
}


.heading{
  /* padding: 20px; */
  display: flex;
  /* align-items: center; */
  margin: 0 auto;
  justify-content: space-between;
}
.heading h1{
  padding: 20px;
  -webkit-animation: fadeIn 4s;
          animation: fadeIn 4s;
  text-decoration: underline;
  /* border-radius: 8px; */
}
.navigation{
  margin-top: 10px;
  padding: 20px;
}
.navigation a{
  -webkit-animation: fadeIn 4s;
          animation: fadeIn 4s;
  color:black;
  background-color: cyan;
  border-radius: 8px;
  margin-top: 20px;
  margin-left: 16px;
  text-decoration: none;
  padding: 6px;
}
.navigation a:hover{
  color: white;
}
.mobile{
  display: none;
  
}
@-webkit-keyframes fadeIn {
  from{
    opacity: 0;
  }
  to{
    opacity: 1;
  }
}
@keyframes fadeIn {
  from{
    opacity: 0;
  }
  to{
    opacity: 1;
  }
}
@media screen and (max-width: 680px){
  .heading{
    display: block;
  }
  .heading h1{
    display: none;
  }
  .mobile{
    display: block;
    margin: 25px;
    text-decoration: underline;
  }
  .navigation a{
    font-size: small;
    margin-top: none;
    padding-top: none;
  }
  .navigation{
    padding: 10px;
    margin-top: none;
    padding-top: none;
  }
}
.myphoto{
    margin-right: 30px;
    width: 50px;
    height: 50px;
    border-radius: 100%;
}
.socialimg{
    width: 25px;
    height: 25px;
    margin-right: 10px;
}
.socialtag{
    margin-right: 20px;
    display: flex;
    align-items: center;
}
.footer{
    display: flex;
    /* align-items: center; */
    position: absolute;
    bottom: 30px;
    left: 30px;
}
.socialLink{
    text-decoration: none;
    color: white;
}
@media screen and (max-width: 680px){
    .footer{
        position: relative;
    }
    .socialtag{
        margin-right: 15px;
    }
    .myphoto{
        margin-right: 30px;
        width: 25px;
        height: 25px;
        border-radius: 100%;
    }
    .socialimg{
        width: 15px;
        height: 15px;
        margin-right: 10px;
    }
    .socialLink{
        font-size: smaller;
    }
}
.cardholder{
    -webkit-animation: fadeIn 5s;
            animation: fadeIn 5s;
    display: flex;
    justify-content: space-around;
}
.projects{
    -webkit-animation: fadeIn 3s;
            animation: fadeIn 3s;
    margin: 20px; 
}
.cardimg{
    border-radius: 10px 0px 0px 10px;
    height: 100%;
    margin-bottom: 20px;
    justify-content: center;
    width: 300px;
}
.card{
    border-radius: 10px;
    display: flex;
    margin: 20px;
    flex: 1 1;
    background-color: orange;
}
.cardtext{
    padding: 20px;
}
.clink{
    background-color: crimson;
    border-radius: 8px;
    margin-left: 16px;
    text-decoration: none;
    padding: 6px 10px;
}
.cardlinks{
    display: flex;
}
.clink a:hover{
    color: purple;
}
@-webkit-keyframes fadeIn {
    from{
      opacity: 0;
    }
    to{
      opacity: 1;
    }
  }
@keyframes fadeIn {
    from{
      opacity: 0;
    }
    to{
      opacity: 1;
    }
  }
  @media screen and (max-width: 680px){
    .cardholder{
        display: block;
    }
    .card{
        display: block;
        
    }
    .projects{
        overflow-y: scroll;
    }
    .cardimg{
       width: 332px;
       border-radius: 10px 10px 0px 0px;
    }
}
.skills{
    margin: 20px;
}
.skillimgs{
    margin-bottom: 5px;
    width: 70%;
    height: 70%;
}
.skillcard{
    align-items: center;
    display: flex;
    -webkit-animation: fadeIn 5s;
            animation: fadeIn 5s;
}
.skillimgholder{
    margin: 20px;
    display: flex;
}
