.cardholder{
    animation: fadeIn 5s;
    display: flex;
    justify-content: space-around;
}
.projects{
    animation: fadeIn 3s;
    margin: 20px; 
}
.cardimg{
    border-radius: 10px 0px 0px 10px;
    height: 100%;
    margin-bottom: 20px;
    justify-content: center;
    width: 300px;
}
.card{
    border-radius: 10px;
    display: flex;
    margin: 20px;
    flex: 1;
    background-color: orange;
}
.cardtext{
    padding: 20px;
}
.clink{
    background-color: crimson;
    border-radius: 8px;
    margin-left: 16px;
    text-decoration: none;
    padding: 6px 10px;
}
.cardlinks{
    display: flex;
}
.clink a:hover{
    color: purple;
}
@keyframes fadeIn {
    from{
      opacity: 0;
    }
    to{
      opacity: 1;
    }
  }
  @media screen and (max-width: 680px){
    .cardholder{
        display: block;
    }
    .card{
        display: block;
        
    }
    .projects{
        overflow-y: scroll;
    }
    .cardimg{
       width: 332px;
       border-radius: 10px 10px 0px 0px;
    }
}